	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapter 4 – The Legion","texte":{"childMarkdownRemark":{"html":"<p>It's now been two weeks since the newcomers arrived. Gabriel learned very quickly with Kémyanne as a teacher. He mastered the attacks and defenses of water and fire, but it was still difficult for him to use his earthly powers. Ezekiel discovered less quickly the potential of his power feeling in competition against Gabriel. Kameron still encouraged him by telling him that his control over the water was limitless. Thomas, on the other hand, was getting used to a new physical form. Thalie was teaching him to get in and out of his bones so that his skin would regenerate faster. Wamien was asking himself more and more questions since Angélique was still not arriving and nobody was answering the phone at the girl's house.</p>\n<p>Clara: I don't think she'll ever join us.<br>\nWamien : I know, but I find it disappointing that the future of this young girl is exclusively decided by her mother.<br>\nClara: That's true, but you can't decide for her, you know, honey.<br>\nWamien : Yeah, I know.</p>\n<p>Clara smiles gently at me and gives me a kiss on the cheek. Hiro arrives a little thoughtful.</p>\n<p>Hiro : Sir, sorry to bother you, but we have problems with the TV, a message is looped on all channels, same thing for phones and radio. You really should see this.<br>\nWamien : All right, I'm with you.</p>\n<p>I wonder what's going on to make everyone panic like that. Hiro escorts me to the living room. A man about 25 was doing a monologue. Hiro's turning up the TV.</p>\n<p>\"Hi, I'm Flynn. I am sending a message through all available means of communication to tell you something very important. The new humans are going to dominate the world, the simple humans are just shit that's done their time. Legion members give us the right to take the lives and possessions of those who oppose us. »</p>\n<p>Wamien : Well...<br>\nHiro : We have to do something...<br>\nWamien: I know Hiro.</p>\n<p>Sarah and Sandrine arrive in panic.</p>\n<p>Sarah: Wam! Have you seen the video that's going around?<br>\nWamien : Yes, I know, Sarah. We're not gonna let them.<br>\nSandrine: I hope so! They're gonna be worse than the Black Eagles...<br>\nClara: How people will treat us because of them, already that the tension was hard to keep stable, with them it will be an impossible thing to do.<br>\nWamien : I need to get in touch with Darakei and Bruno right away.<br>\nSam : We're not going to have a choice to drive to see them.<br>\nWamien : Yeah.<br>\nHiro: Maybe not, communications are coming back little by little and we'd better stay safe here.<br>\nWamien: I agree with you, Hiro.</p>\n<p>I leave the room with Clara and go into communication with Darakei and Bruno. Darakei answers quickly and it remains without answers on the side of the SIAM.</p>\n<p>Wamien: I guess you saw the message that came in the last hour.<br>\nDarakei : Yes, I already have a group ready to intervene if there is a problem with The Legion. And then after checking the identity of the person who was speaking, his full name is Flynn Gamay and I strongly advise you to stay as far away from him as possible. Its power is to control and change the shape of the metal.<br>\nWamien : Seriously? Shit... It means there's not a lot that can stop him...<br>\nDarakei : We are still looking for a way to prevent his power to affect metals otherwise he would already be behind bars.<br>\nWamien : Constant exposure to the inhibitor?<br>\nDarakei: He will easily break the gas capsules.<br>\nClara : And you don't know if one person has the power to cancel the powers of others?<br>\nDarakei : No, I have lots of individuals who have power potential of all kinds, but none of this type.<br>\nWamien : Shit...<br>\nDarakei : And then as soon as I managed to reach Bruno, I would send him the information about Flynn Gamay. Do you think you can locate the area where the message was broadcast?<br>\nWamien : No doubt, but it might take some time. I'll keep a team to intervene if necessary. I also fear that the village will be the target of future attacks.<br>\nClara: We'll lay low and keep it quiet.<br>\nDarakei: Keep an eye on people entering and leaving the village night and day to warn in case of malicious visits.<br>\nWamien : Thanks for the idea Darakei</p>\n<p>Darakei nods in thanks.</p>\n<p>Darakei: I have to go. We'll get back to you later if we need anything.<br>\nWamien : No problem, goodbye.</p>\n<p>The communication ends and Clara looks at me, clearly worried.</p>\n<p>Clara: You stay away from that Flynn, is that clear?<br>\nWamien : Yes, don't worry about it, sweetheart.<br>\nClara: I'll inform the others of the information we have, I'll let you locate where the message came from.<br>\nWamien : Okay, could you ask Kameron to come here too, two of you will go faster to locate him.<br>\nClara: No worries.</p>\n<p>Clara kisses me on the cheek and leaves the room. I'm starting to research where the message came from. Kameron arrives meanwhile with Ezekiel, as he is his apprentice, they must stay together most of the time. Three of us get on the task of locating the source of the message.</p>"}},"chapitreAvant":"chapter-3-the-apprentices","chapitreApres":"chapter-5-the-attack","nomRoman":"the-first-cyborg-volume-2","slug":"chapter-4-the-legion","codeChapitre":"H0002C004","equivalentUrl":"/histoires/chapitre/chapitre-4-la-legion"}},"pathContext":{"slug":"chapter-4-the-legion"}}

